import { mapState } from 'vuex'


export default {
	data() {
		return {
			timer: null,
			counter: 5
		}
	},
	computed: {
		...mapState(['hasLogin'])
	},
	watch: {
		counter(newV, oldV) {
			if(newV === 0) {
				if(this.hasLogin) {
					uni.switchTab({
						url: '/pages/index/index'
					})
				}else {
					uni.reLaunch({
						url: '/components/content/login/login'
					})
				}
			}
		}
	},
	methods: {
		stopCounter() {
			clearInterval(this.timer)
			if(this.hasLogin) {
				uni.switchTab({
					url: '/pages/index/index'
				})
			}else {
				uni.reLaunch({
					url: '/components/content/login/login'
				})
			}
		},
		
	},
	onLoad() {
		console.log('startup启动页onLoad')
		
		
		this.timer = setInterval(() => {
			if(this.counter <= 0) {
				clearInterval(this.timer)
			}else {
				this.counter -= 1
			}
			
		}, 1000)
	},
	onUnload() {
		clearInterval(this.timer)
	}
}
